.compare-bikes {
  &__products {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    grid-column: 1 /-1;

    @include on-tablet {
      grid-column: 2 /-2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }

  &__title--margin-bottom {
    @include on-tablet {
      margin-bottom: 48px;
    }

    @include on-desktop {
      margin-bottom: 56px;
    }
  }
}
